import React from 'react';
import Component from 'react-pure-render/component';

import KindbeetleIcon from '../icons/logo-kindbeetle.svg';
import FacebookIcon from '../icons/facebook.svg';
import EmailIcon from '../icons/email.svg';
import text from '../../data/text.yml';
import links from '../../data/links.yml';
import linkify from './linkify';

const lang = process.env.LANG;


export default class Header extends Component {
  render() {
    return (
      <div className={styles.root}>
        <div className={styles.content}>
          <div className={styles.headTitle}>
            <KindbeetleIcon className={styles.title} />
            <a href={links.langLink[lang]}>
              <div className={styles.iconLang}>
                {linkify(text.langICON[lang])}
              </div>
            </a>
            <a href={`mailto:${links.mailto}`}>
              <div className={styles.iconEmail}>
                <EmailIcon />
              </div>
            </a>
            <a href={links.facebook} target='_blank'>
              <div className={styles.iconFacebook}>
                <FacebookIcon />
              </div>
            </a>
          </div>
          <div className={styles.headMotto}>
            <h2>{linkify(text.header[lang])}</h2>
          </div>
          <div className={styles.teamDesc}>
            {text.teamDesc.map((item, index) =>
              <div key={index}>
                <h3>{linkify(item.title[lang])}</h3>
                <p>{linkify(item.text)}</p>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

}



